import { useState, useCallback, useEffect } from "react";
import { Badge } from "antd";
import { MailAppService } from "../../../services/adminService";
import ManageMailAppModifyComponent from "./MailApp_ModifyComponent";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import TableComponent from "../../core/TableNewComponent";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { Fn } from "../../../helper/function";
export default function ManageMailAppIndexComponent() {
    const column = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Tên", key: "name", render: (data: string, row: any) =>
                <MyButton type="link" value={data} onClick={() => {
                    handleClickEdit(row.key);
                }} />
        }),
        new TableColumn({ title: "Chú thích", key: "comment" }),
        new TableColumn({ title: "Key", key: "secret" }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (row: number) => {
                var badge: any = {};
                if (row === 1) {
                    badge = {
                        msg: 'Đang hoạt động',
                        color: 'blue'
                    };
                } else {
                    badge = {
                        msg: 'Không hoạt động',
                        color: 'red'
                    };
                }
                return <Badge text={badge.msg} color={badge.color} />
            }
        }),
        new TableColumn({
            title: "Ngày thêm", key: "createdAt", render: (row: any) =>
                <UIFormatDateTimeVNComponent value={row} />
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối", key: "updatedAt", render: (row: any) =>
                <UIFormatDateTimeVNComponent value={row} />
        })
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [visibleAdd, setVisibleAdd] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let pagination = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        MailAppService.GetList(pagination)
            .then((res: any) => {
                if (res.isSuccess) {
                    setPageInfo(new PageInfoModel(res));
                    setData(res.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false));
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickEdit(key: any) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function handleClickAddApp() {
        setVisibleAdd(true);
    }

    return (
        <MyBox
            title="Quản lí app mail"
            isLoading={isLoading}
            buttonList={() => (<MyButton
                type="primary"
                onClick={handleClickAddApp}
                value="Thêm app mới" />)}
        >
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData}
                orderBy={orderBy}
                setOrderBy={setOrderBy} />

            <ManageMailAppModifyComponent
                keyData={undefined}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                visible={visibleAdd}
                setVisible={setVisibleAdd} />

            <ManageMailAppModifyComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                visible={visibleEdit}
                setVisible={setVisibleEdit} />
        </MyBox>
    );
}