import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { MailAppService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import AssignTemplateComponent from "./AssignTemplateComponent";

export default function ManageMailAppModifyComponent(props: any) {
    const { isLoading, setIsLoading, keyData, setKeyData, visible, setVisible, loadData } = props;

    const [name, setName] = useState('');
    const [comment, setComment] = useState('');
    const [keySecret, setKeySecret] = useState('');
    const [status, setStatus] = useState(0);

    const [visibleTemplate, setVisibleTemplate] = useState(false);
    const options = [
        {
            key: "active",
            label: "Đang hoạt động",
            value: 1
        },
        {
            key: "inactive",
            label: "Không hoạt động",
            value: 0
        }
    ]
    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            MailAppService.GetDetail(keyData)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setName(rs.data.name);
                        setComment(rs.data.comment);
                        setKeySecret(rs.data.secret);
                        setStatus(rs.data.status);
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyData, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function onClose() {
        setVisible(false);
        if (keyData !== undefined) {
            setKeyData(undefined);
        }
    }

    function handleSubmitForm() {
        setIsLoading(true);
        var body = {
            name: name,
            comment: comment,
            status: status
        };

        if (keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        MailAppService.Add(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    loadData();
                    Fn.PrintMsgSuccessV2(rs);
                }
            })
            .finally(() => setIsLoading(false))
    }

    function Edit(body: any) {
        MailAppService.GetDetail(keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestampV2(rs, body);
                    MailAppService.Edit(keyData, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                loadData();
                                Fn.PrintMsgSuccessV2(rs);
                            }

                        })
                        .finally(() => setIsLoading(false));
                } else setIsLoading(false)
            })
    }

    function handleAssignTemplate() {
        setVisibleTemplate(true);
    }

    function handleDelete() {
        Swal.fire({
            title: "Xác nhận xóa!",
            text: "Bạn có muốn xóa app không?",
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                DeleteApp();
            }
        })
    }

    function DeleteApp() {
        setIsLoading(true);
        MailAppService.GetDetail(keyData)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    MailAppService.Delete(keyData, Fn.GetTimestampV2(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                loadData();
                                Fn.PrintMsgSuccessV2(rs);
                                onClose();
                            };
                        })
                        .finally(() => setIsLoading(false));
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            title={keyData === undefined ? "Thêm app mới" : "Cập nhật thông tin app"}
            open={visible}
            onClose={onClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên App"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Input
                    label="Chú thích"
                    value={comment}
                    setValue={setComment} />

                <MyFormItem.Input
                    label="Mã bí mật"
                    readOnly={true}
                    value={keySecret}
                    setValue={setKeySecret} />

                <MyFormItem.Select
                    label="Trạng thái"
                    value={status}
                    setValue={setStatus}
                    options={options} />

                <Row>
                    <Col span={24}>
                        <MyButton
                            style={{ marginBottom: "10px" }}
                            isLoading={isLoading}
                            type="primary"
                            value="Chọn template"
                            onClick={handleAssignTemplate} />
                    </Col>
                </Row>

                <MyButton
                    type="success"
                    isLoading={isLoading}
                    htmlType="submit"
                    value={keyData === undefined ? "Thêm" : "Cập nhật"} />

                <MyButton
                    type="danger"
                    isLoading={isLoading}
                    onClick={handleDelete}
                    value="Xóa" />
            </Form >

            <AssignTemplateComponent
                keyData={keyData}
                visible={visibleTemplate}
                setVisible={setVisibleTemplate}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />
        </Drawer >
    );
}